import Ellipse from '@/components/ellipse';
import Header from '@/components/header';
import Headline from '@/components/headline';
import Mission from '@/components/mission';
import WaveForm from '@/components/waveform';
import {
  Box,
  Center,
  ChakraProvider,
  HStack,
  Spacer,
  Text,
  VStack,
} from '@chakra-ui/react';
import theme from '../theme';
import { useInView } from 'react-intersection-observer';
import 'animate.css';
import Service from '@/components/service';
import { graphql, PageProps } from 'gatsby';
import Member from '@/components/member';
import News from '@/components/news';
import CompanyOverview from '@/components/companyOverview';
import Footer from '@/components/footer';

const Home: React.FC<PageProps<GatsbyTypes.HomeQuery>> = ({ data }) => {
  const { ref: missionRef, inView: missionInView } = useInView({
    rootMargin: '-100px',
    triggerOnce: true,
  });
  const { ref: serviceRef, inView: serviceInView } = useInView({
    rootMargin: '-300px',
    triggerOnce: true,
  });
  const { ref: memberRef, inView: memberInView } = useInView({
    rootMargin: '-200px',
    triggerOnce: true,
  });
  const { ref: newsRef, inView: newsInView } = useInView({
    rootMargin: '-300px',
    triggerOnce: true,
  });
  const { ref: companyRef, inView: companyInView } = useInView({
    rootMargin: '-200px',
    triggerOnce: true,
  });

  return (
    <>
      <ChakraProvider theme={theme}>
        <Center>
          <Header w="1000px" position="absolute" top={0} />
        </Center>

        <main>
          <Ellipse
            isReverse={false}
            w="484px"
            h="655.68px"
            position="absolute"
            left="-100px"
            top="-200px"
          />

          <Box as="section">
            <HStack h="100vh" w="1000px" m="auto">
              <Text
                className="animate__animated animate__fadeIn"
                style={{ animationDelay: '0.2s', animationDuration: '1s' }}
                color="grey.900"
                fontSize={36}
              >
                
                <br />
                
              </Text>
              <Spacer />
              <WaveForm h="60vh" />
            </HStack>
          </Box>

          <Box as="section" ref={missionRef} pb={20}>
            {missionInView ? (
              <Box
                className="animate__animated animate__fadeIn"
                style={{ animationDelay: '0.2s' }}
              >
                <Headline title="Mission"></Headline>
                <Mission
                  image={data.mission?.childImageSharp?.gatsbyImageData!}
                  title=""
                  description={
                    <>
                      
                      
                      
                      <br />
                      <br />
                      
                      
                      
                      <br />
                      <br />
                      
                       
                    </>
                  }
                  m="auto"
                  mt={10}
                />
              </Box>
            ) : (
              <Box h="300px" />
            )}
          </Box>

          <Box
            id="service"
            as="section"
            ref={serviceRef}
            bg="#F5F5F5"
            py={20}
            position="relative"
          >
            <Ellipse
              isReverse={false}
              w="250px"
              h="250px"
              position="absolute"
              right={100}
              top={-20}
              zIndex={1}
            />
            {serviceInView ? (
              <Box
                className="animate__animated animate__fadeIn"
                style={{ animationDelay: '0.2s' }}
              >
                <Headline title="Service"></Headline>
                <Service
                  image={data.shadowingSaver?.childImageSharp?.gatsbyImageData!}
                  name="Shadowing Saver"
                  description="LMS"
                  m="auto"
                  mt={16}
                />
              </Box>
            ) : (
              <Box h="300px" />
            )}
          </Box>

          <Box id="member" as="section" ref={memberRef} py={20}>
            {memberInView ? (
              <Box style={{ animationDelay: '0.2s' }}>
                <Headline title="Member" mb={16}></Headline>
                <Member
                  className="animate__animated animate__fadeInUp"
                  image={data.inoue?.childImageSharp?.gatsbyImageData!}
                  title=""
                  name=""
                  nameEn="Yusuke Inoue"
                  profile={
                    <>
                      
                      <br />
                      WebWeb
                      <br />
                      
                    </>
                  }
                  isImageRight={false}
                  m="auto"
                  maxW="1000px"
                  mb={16}
                />
                <Member
                  className="animate__animated animate__fadeInUp"
                  image={data.mission?.childImageSharp?.gatsbyImageData!}
                  title=""
                  name=""
                  nameEn="Hiroaki Mizuno"
                  profile={
                    <>
                      CMS
                      <br />
                      SIer
                      <br />
                      Web
                      <br />
                    </>
                  }
                  isImageRight={true}
                  m="auto"
                  maxW="1000px"
                />
              </Box>
            ) : (
              <Box h="300px" />
            )}
          </Box>

          <Box
            id="news"
            as="section"
            ref={newsRef}
            bg="#F5F5F5"
            py={20}
            position="relative"
          >
            <Ellipse
              isReverse={true}
              w="384px"
              h="500.68px"
              position="absolute"
              left="-120px"
              top="-300px"
              zIndex={1}
            />
            {newsInView ? (
              <Box
                className="animate__animated animate__fadeIn"
                style={{ animationDelay: '0.2s' }}
              >
                <Headline title="News"></Headline>
                <VStack spacing={4}>
                  <News
                    date={'2022.07.04'}
                    title={''}
                  />
                </VStack>
              </Box>
            ) : (
              <Box h="300px" />
            )}
          </Box>

          <Box id="company" as="section" ref={companyRef} py={20}>
            {companyInView ? (
              <Box
                className="animate__animated animate__fadeIn"
                style={{ animationDelay: '0.2s' }}
              >
                <Headline title="Company" mb={16}></Headline>
                <CompanyOverview w="1000px" />
              </Box>
            ) : (
              <Box h="300px" />
            )}
          </Box>

          <Footer />
        </main>
      </ChakraProvider>
    </>
  );
};

export const query = graphql`
  query Home {
    mission: file(relativePath: { eq: "mission.png" }) {
      childImageSharp {
        gatsbyImageData(
          blurredOptions: { width: 100 }
          width: 600
          placeholder: BLURRED
        )
      }
    }
    shadowingSaver: file(relativePath: { eq: "service/shadowing_saver.png" }) {
      id
      childImageSharp {
        gatsbyImageData(
          blurredOptions: { width: 100 }
          width: 600
          placeholder: BLURRED
        )
      }
    }
    inoue: file(relativePath: { eq: "member/inoue.png" }) {
      id
      childImageSharp {
        gatsbyImageData(
          blurredOptions: { width: 100 }
          width: 600
          placeholder: BLURRED
        )
      }
    }
  }
`;

export default Home;
